<template>
  <div id="publish">
    <el-row v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">

          <el-form-item label="应用图标" prop="icon">
            <div class="upload-demo">
              <img :src="form.icon" class="show-image" v-if="form.icon && form.icon != ''"/>
              <vue-base64-file-upload
                v-if="reload"
                accept="image/png,image/jpeg"
                :disable-preview="true"
                input-class="show-input"
                :max-size="iconMaxSize"
                @size-exceeded="iconOnSizeExceeded"
                placeholder="点击上传应用图标（必填）"
                @file="onFile"
                @load="iconOnLoad" />
              <div class="el-upload__tip">
                推荐使用正方形图片，尺寸为 480px × 480px,只能上传jpg/png文件，且不超过30k,
                上传图片前，建议先在 <a href="https://tinypng.com/" target="_blank">tinypng.com</a> 上压缩一下
              </div>
            </div>
          </el-form-item>

          <el-form-item label="应用名称" prop="name">
            <el-input minlength="1" maxlength="150" v-model="form.name" placeholder="请输入应用名称（必填）"></el-input>
          </el-form-item>

          <el-form-item label="应用类型" prop="dtype">
            <el-select v-model="form.dtype" placeholder="请选择">
              <el-option
                v-for="item in dtypeOptions"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="应用访问地址" prop="url">
            <el-input minlength="1" maxlength="350" v-model="form.url" placeholder="请输入应用访问地址（必填）"></el-input>
          </el-form-item>

          <el-form-item label="作者名称或昵称" prop="userName">
            <el-input minlength="1" maxlength="150" v-model="form.userName" placeholder="请输入作者名称或昵称（必填）"></el-input>
          </el-form-item>

          <el-form-item label="合约地址" prop="addr">
            <el-input minlength="1" maxlength="210" v-model="form.addr" placeholder="请输入合约地址，多个地址请用半角逗号隔开（非必填）"></el-input>
          </el-form-item>

          <el-form-item label="合约txhash" prop="txhash">
            <el-input minlength="1" maxlength="210" v-model="form.txhash" placeholder="请输入合约txhash，多个地址请用半角逗号隔开（非必填）"></el-input>
          </el-form-item>

          <el-form-item label="应用描述" prop="desc">
            <el-input type="textarea" :autosize="{ minRows: 5, maxRows: 10}"
                      minlength="1" maxlength="600"
                      v-model="form.desc" placeholder="请输入应用描述（非必填）">
            </el-input>
          </el-form-item>

          <el-form-item label="第几周提交" prop="week">
            <el-select v-model="form.week" placeholder="请选择">
              <el-option
                v-for="item in weekOptions"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="被拒原因" prop="reason">
            <el-input type="textarea" :autosize="{ minRows: 5, maxRows: 10}"
                      minlength="1" maxlength="600"
                      v-model="form.reason" placeholder="请输入被拒原因（非必填），把经验分享给其他小伙伴们，让我们以后的每个应用都能顺利通过！">
            </el-input>
          </el-form-item>

          <el-form-item label="被拒截图" prop="reasonimg">
            <div class="upload-demo">
              <img :src="form.reasonimg" class="show-image-bj" v-if="form.reasonimg && form.reasonimg != ''"/>
              <vue-base64-file-upload
                v-if="reload"
                accept="image/png,image/jpeg"
                :disable-preview="true"
                input-class="show-input"
                :max-size="reasonimgMaxSize"
                @size-exceeded="reasonimgOnSizeExceeded"
                placeholder="点击上传应用图标（必填）"
                @file="onFile"
                @load="reasonimgOnLoad" />
              <div class="el-upload__tip">
                只能上传jpg/png文件，且不超过50k,
                上传图片前，建议先在 <a href="https://tinypng.com/" target="_blank">tinypng.com</a> 上压缩一下
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitNode" v-if="!queryId">提交DApp</el-button>
            <el-button type="primary" @click="modNode" v-if="queryId">修改DApp</el-button>
            <el-button @click="resetForm('form')" v-if="!queryId">重置</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      queryId: null,
      reload: true,
      dtypeOptions: ['应用', '游戏', '其他'],
      weekOptions: ['第一周', '第二周', '第三周', '第四周', '第五周', '第六周', '第七周', '第八周'],
      // 单位M 30k 0.03M
      iconMaxSize: 0.03,
      // 单位M 100k 0.03M
      reasonimgMaxSize: 0.05,
      loading: false,
      form: {
        icon: '',
        name: '',
        dtype: '',
        url: '',
        userName: '',
        addr: '',
        txhash: '',
        desc: '',
        week: '',
        reason: '',
        reasonimg: ''
      },
      formRules: {
        icon: [
          {required: true, message: '请上传应用图标', trigger: 'blur'}
        ],
        name: [
          {required: true, message: '请输入应用名称', trigger: 'blur'},
          {min: 1, max: 150, message: '长度在 1 到 150 个字符', trigger: 'blur'}
        ],
        dtype: [
          {required: true, message: '请选择应用类型', trigger: 'blur'},
          {min: 1, max: 150, message: '长度在 1 到 150 个字符', trigger: 'blur'}
        ],
        url: [
          {required: true, message: '请输入应用地址', trigger: 'blur'},
          {min: 1, max: 350, message: '长度在 1 到 350 个字符', trigger: 'blur'}
        ],
        userName: [
          {required: true, message: '请输入作者名称或昵称', trigger: 'blur'},
          {min: 1, max: 150, message: '长度在 1 到 150 个字符', trigger: 'blur'}
        ],
        addr: [
          {required: true, message: '请输入合约地址', trigger: 'blur'},
          {min: 1, max: 210, message: '长度在 1 到 210 个字符', trigger: 'blur'}
        ],
        txhash: [
          {min: 1, max: 210, message: '长度在 1 到 210 个字符', trigger: 'blur'}
        ],
        desc: [
          {min: 1, max: 600, message: '长度在 1 到 600 个字符', trigger: 'blur'}
        ],
        week: [
          {required: true, message: '请输入第几周提交', trigger: 'blur'},
          {min: 1, max: 150, message: '长度在 1 到 150 个字符', trigger: 'blur'}
        ],
        reason: [
          {required: true, message: '请输入被拒原因', trigger: 'blur'},
          {min: 1, max: 600, message: '长度在 1 到 600 个字符', trigger: 'blur'}
        ],
        reasonimg: [
          {required: true, message: '请上传被拒截图', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 头像上传
    iconOnSizeExceeded (size) {
      let s = size * 1000;
      this.showErr(`应用图标大小 ${s}k 超出限额 30k!`);
    },
    onFile (file) {
      console.log('onFile==', file); // file object
    },
    iconOnLoad (dataUri) {
      this.form.icon = dataUri;
    },
    // 头像上传
    reasonimgOnSizeExceeded (size) {
      let s = size * 1000;
      this.showErr(`被拒截图大小 ${s}k 超出限额 50k!`);
    },
    reasonimgOnLoad (dataUri) {
      this.form.reasonimg = dataUri;
    },
    // 返回首页
    returnHome: function () {
      this.$router.push({name: 'index'})
    },
    submitNode: function () {
      var vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('请先登录钱包！');
        return false;
      }
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var f = vueThis.form;
          let params = [f.name, f.dtype, f.icon, f.url, f.userName, f.addr, f.txhash, f.desc, f.week, f.reason, f.reasonimg];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'addDapp', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('发布DApp成功');
            vueThis.$router.push({name: 'mine'})
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('发布DApp失败' + error);
          });
        } else {
          vueThis.showErr('请完善表单必填字段');
        }
      })
    },
    modNode: function () {
      var vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('请先登录钱包！');
        return false;
      }
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var f = vueThis.form;
          let params = [vueThis.queryId, f.name, f.dtype, f.icon, f.url, f.userName, f.addr, f.txhash, f.desc, f.week, f.reason];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'updateDapp', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('修改DApp成功');
            vueThis.$router.push({name: 'mine'})
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('修改DApp失败' + error);
          });
        } else {
          vueThis.showErr('请完善表单必填字段');
        }
      })
    },
    getDetail: function (id) {
      let vueThis = this;
      if (!id) {
        return;
      }
      vueThis.loading = true;
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getDapp', params: [id]}).then((ret) => {
        let data = JSON.parse(ret);
        vueThis.form = data;
        vueThis.loading = false;
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('获取详情失败' + error);
      });
    },
    resetForm: function (formName) {
      var vueThis = this;
      vueThis.reload = false;
      vueThis.$refs[formName].resetFields();
      _.delay(function () {
        vueThis.reload = true;
      }, 300)
    }
  },
  created () {
    this.queryId = this.$route.query.id;
    this.getDetail(this.queryId)
  },
  computed: mapState([
    'accounts'
  ])
}
</script>

<style>
  #publish {
  }

  #publish  .el-form--label-top .el-form-item__label{
    padding: 0px;
  }

  #publish .show-image{
    border-radius:20px;
    width: 120px;
    height: 120px;
    margin-bottom: 10px;
    object-fit: cover;
    box-shadow:0 4px 3px -2px rgba(0,0,0,.8), 0 0 3px 1px rgba(0,0,0,.05);
    transition:all .4s;
    vertical-align: middle;
    border-style: none;
  }

  #publish .show-image-bj{
    max-width: 220px;
    max-height: 320px;
    margin-bottom: 10px;
    object-fit: cover;
    vertical-align: middle;
    border-style: none;
  }

  #publish .show-input{
    background-color:#fbfdff;
    border:1px dashed #c0ccda;
    border-radius:4px;
    box-sizing:border-box;
    cursor:pointer;
    padding: 10px;
  }

  #publish .text-here {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #publish .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }
</style>



